<template>
	<view class="news_detail">
		<text class="title">{{detail.title}}</text>
		<view class="info">
			<text>发表时间：{{detail.add_time | formateDate}}</text>
			<text>浏览：{{detail.click}}</text>
		</view>
		<view class="content">
			<!-- 富文本 -->
			<rich-text :nodes="detail.content"></rich-text>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id: 0,
				detail: {}
			}
		},
		methods: {
			async getNewDetail() {
				const res = await this.$myRequest({
					url: '/api/getnew/' + this.id
				})
				this.detail = res.data.message[0]
			}
		},
		onLoad(options) {
			this.id = options.id
			this.getNewDetail()
		}
	}
</script>

<style>
	.news_detail {
		font-size: 30rpx;
		padding: 0 20rpx;
	}

	.news_detail .title {
		text-align: center;
		width: 710rpx;
		display: block;
		margin: 20rpx 0;
	}

	.news_detail .info {
		display: flex;
		justify-content: space-between;
	}
</style>
